export default {
  data() {
    return {
        userInfo:'',
        selectList:[]
    };
  },
  onLoad() {
   this.fetchData();
   uni.getStorage({
     key:'buylist',
     success: (res) => {
       console.log(res);
       this.selectList = res.data;
     }
   })
  },
  computed:{
    defAddres(){
      let province = this.userInfo.province;
      let city = this.userInfo.city;
      let district = this.userInfo.district;
      let address = this.userInfo.address;
      return province+city+district+address;
    },
    toatleMoney(){
      let money = 0;
      this.selectList.forEach(item => {
        money += parseInt(item.goods_price);
      })
      return money;
    }
  },
  methods:{
     payment(){
      uni.navigateTo({
        url:"/pages/tabBar/shCart/payment/payment"
      })
    },
    //获取默认地址信息
    fetchData(){
      let url = "address/defaultAddress";
      this.$Request.get(url).then( res => {
       if(res && res.code == 200){
         this.userInfo = res.data;
        }
      })
    },
     //切换到地址页面
     showAddre(){
       uni.navigateTo({
         url:"/pages/address/address"
       })
     }
  }
}
